@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: 100%;
  height: 100vh;
}

.quotesLayer {
    position: absolute;
    z-index: 9;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.7)
}
.quotesLayer > div {
    width: 50%;
    height: 60%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    position: absolute;
    box-shadow: 0 6px 6px rgba(0,0,0,0.6);
}
.quoteStyle {
    width: 100%;
    height: 70%;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0 15px;
    margin-top: 5%;
}
.author {
    text-align: center;
    margin: 0;
}
.quoteBtn {
    position: absolute;
    bottom: 10px;
    right: 10px;
    transition: all 0.3s;
}
.quoteBtn:hover {
    border-bottom: 1px solid;
}
.quoteBtn:focus {
    box-shadow: none;
    outline: none;
}

@media screen and (max-width: 320px) {
    .quoteBtn {
        font-size: 10px;
    }
    .quotesLayer > div {
        height: 40%;
    }
    .quoteStyle {
        font-size: 16px;
    }
    .author {
        font-size: 12px;
    }
}
@media screen and (max-width: 425px) {
    .quotesLayer > div {
        height: 35%;
        width: 80%;
    }
}
@media screen and (max-width: 768px) {
    .quotesLayer > div {
        height: 45%;
    }
}
.layerContainer {
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}
.tile {
    width: 24%;
    height: 35%;
    float: left;
    /* position: absolute; */
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0.5%;
    font-family: 'Lato', sans-serif;
}

@media screen and (max-width: 320px) {
    #main, #layer {
        width: 65%;
    }
}
@media screen and (max-width: 425px) {
    .tile {
        width: 32%;
        height: 25%;
        font-size: 12px;
    }
}
@media screen and (max-width: 768px) {
    .quotesLayer > div {
        height: 45%;
    }
}
